.navbar-no-background {
  background: rgba(0, 0, 0, 0.5);
  min-height: 0;
  position: relative;
  overflow: hidden;
}
.context-style {
  text-align: justify;
}
.context-style p {
  margin-bottom: 1.5em;
  font-weigth: strong;
  font-size: 16px;
}
.context-style p ul {
  list-style: disc;
}
